<template>
  <div class="nong-home">
    <NavBar />
    <div class="nong-home--title">我的金币</div>
    <div class="nong-home--skeleton">
      <van-skeleton title avatar :row="3" :loading="JSON.stringify(accountInfo).length === 2">
        <div class="skeleton-card theme-card">
          <van-row>
            <van-col span="20" class="card-account">
              <div class="account-logo"></div>
              <div class="account-info">
                <div class="info-name theme-account-name">{{ accountInfo.payAccountName }}</div>
                <div class="info-number theme-account-merchantNo">{{ accountInfo.merchantNo }}</div>
              </div>
            </van-col>
            <van-col span="4">
              <van-tag class="tag theme-tag-color" @click="isShow = true">
                切换
                <van-icon name="exchange" class="tag-change" />
              </van-tag>
            </van-col>
          </van-row>
          <van-row class="card-money">
            <van-col span="12">
              <div class="account-title theme-account-title">账户余额</div>
              <div class="money-color theme-account-money">
                {{ accountInfo.balance | number2Yen }}
              </div>
            </van-col>
            <van-col span="10" offset="2">
              <div class="account-title theme-account-title">可提现金额</div>
              <div class="money-color theme-account-money">
                {{ accountInfo.withdrawAmount | number2Yen }}
              </div>
            </van-col>
          </van-row>
        </div>
        <van-row class="skeleton-domain">
          <van-col span="12" class="domain-btn">
            <van-button type="warning" class="btn recharge theme-btn-bg" @click="go2Recharge">
              充&nbsp;值
            </van-button>
          </van-col>
          <van-col span="12" class="domain-btn">
            <span class="btn-label theme-btn-label" v-if="isAuto"></span>
            <van-button
              type="warning"
              class="btn widthdrawal theme-btn-color"
              @click="go2Withdraw"
              >{{ btnContent }}</van-button
            >
          </van-col>
        </van-row>
      </van-skeleton>
    </div>
    <van-popup
      safe-area-inset-bottom
      round
      position="bottom"
      class="nong-home--popup"
      v-model="isShow"
    >
      <div class="popup-close">
        <van-button
          type="info"
          icon="arrow-down"
          class="arrow-down"
          @click="isShow = false"
        ></van-button>
      </div>
      <van-radio-group v-model="accountID">
        <van-cell-group>
          <van-cell
            clickable
            title-class="radio-title-class theme-account-name"
            label-class="radio-label-class theme-account-merchantNo"
            v-for="(account, index) in accountList"
            :key="index"
            @click="changeAccount(index)"
          >
            <div slot="title">{{ account.payAccountName }}</div>
            <div slot="label">{{ account.merchantNo }}</div>
            <van-radio slot="icon" class="radio-left-color theme-radio-color" :name="index" />
          </van-cell>
        </van-cell-group>
      </van-radio-group>
    </van-popup>
  </div>
</template>

<script>
import NavBar from '../navbar'
import {
  Button,
  Row,
  Col,
  Icon,
  Tag,
  Toast,
  Skeleton,
  Popup,
  RadioGroup,
  Radio,
  CellGroup,
  Cell
} from 'vant'
import { getAccountList, getAccountInfo, getMoney } from '@/api'
import { resFilter } from '@/utils/tool' // 响应过滤工具函数
export default {
  name: 'home',
  components: {
    [Button.name]: Button,
    [Row.name]: Row,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Tag.name]: Tag,
    [Skeleton.name]: Skeleton,
    [Popup.name]: Popup,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    NavBar
  },
  data() {
    return {
      isShow: false, // 账户列表选择组件是否展示
      accountList: [], // 账户列表
      accountID: 0,
      accountCode: 'Slo_001', // 编码
      accountInfo: {}, // 展示账户信息详情
      companyPaymentState: '', // 企业付款状态 NO: 未开通;SUCCESS: 已开通
      autoSettle: '', // 开通自动提现 1: 自动;2: 手动
      companyPaymentAutoSettle: '' // 企业付款自动提现状态 OPEN: 开启; CLOSE: 关闭
    }
  },
  computed: {
    isAuto() {
      // 自动角标显示判断
      let isTrue = false
      switch (this.companyPaymentState) {
        case 'NO':
          isTrue = this.autoSettle === '1' ? true : false
          break
        case 'SUCCESS':
          isTrue = this.companyPaymentAutoSettle === 'OPEN' ? true : false
          break
        default:
          false
          break
      }
      return isTrue
    },
    btnContent() {
      // 判断按钮显示文字
      return this.companyPaymentState === 'SUCCESS' ? '企业付款' : '提现'
    }
  },
  watch: {
    accountID(index) {
      // 账户信息切换监听
      this.accountInfo = this.accountList[index]
      this.accountCode = this.accountList[index].code
      this.reloadAccountInfo(this.accountCode)
    }
  },
  created() {
    // this.load()
    setTimeout(() => {
      this.accountInfo = {
        payAccountName: '幻海',
        merchantNo: 'Slo_001',
        balance: 99999.99,
        withdrawAmount: 90000
      }

      this.accountList = [
        {
          code: 'Slo_001',
          payAccountName: '幻海001',
          merchantNo: 'Slo_001',
          balance: 99999.99,
          withdrawAmount: 90000
        },
        {
          code: 'Slo_002',
          payAccountName: '幻海002',
          merchantNo: 'Slo_002',
          balance: 2,
          withdrawAmount: 2
        },
        {
          code: 'Slo_003',
          payAccountName: '幻海003',
          merchantNo: 'Slo_003',
          balance: 3,
          withdrawAmount: 3
        },
        {
          code: 'Slo_004',
          payAccountName: '幻海004',
          merchantNo: 'Slo_004',
          balance: 4,
          withdrawAmount: 4
        }
      ]
    }, 500)
  },
  methods: {
    changeAccount(index) {
      // 账户切换
      this.accountID = index
      this.isShow = false
    },
    go2Recharge() {
      // 跳转到充值页面
      this.$router.push({
        path: `/recharge/${this.accountCode}`,
        query: {
          name: encodeURIComponent(this.accountInfo.payAccountName),
          merchantNo: this.accountInfo.merchantNo
        }
      })
    },
    go2Withdraw() {
      // 跳转到提现页面
      this.isAuto
        ? Toast('该商户已开通自动提现,\n请前往商户PC端关闭自动提现后重试')
        : this.companyPaymentState === 'SUCCESS' &&
          (!this.accountInfo.privateNolegalpersonAccount ||
            this.accountInfo.privateNolegalpersonAccount.length === 0)
        ? Toast('企业付款未绑卡,\n请绑卡后重试')
        : this.$router.push(`/withdraw/${this.accountCode}`)
    },
    renderInfo(info) {
      // 单纯赋值
      this.accountInfo = info
      this.companyPaymentState = info.companyPaymentState
      this.autoSettle = info.autoSettle
      this.companyPaymentAutoSettle = info.companyPaymentAutoSettle
    },
    async reloadAccountInfo(accountCode) {
      // 获取商编所有信息 + 金额
      try {
        let merchantNoOjb = resFilter(await getAccountInfo(accountCode), {}) // 获取商编的所有信息
        JSON.stringify(merchantNoOjb).length === 2 && Toast.fail('获取账户信息失败')

        let money = resFilter(await getMoney(accountCode), {}) // 获取账户余额和可提现金额
        let info = { ...merchantNoOjb, ...money } // 信息组合

        this.renderInfo(info)
      } catch (e) {
        console.info(e.message) // 异常获取
      }
    },
    async load() {
      // 信息初始化
      try {
        let res = resFilter(await getAccountList())
        this.accountList = res
        this.accountList[0] = this.accountList[0] || {}
        this.accountCode = this.accountList[0].code || ''
        this.accountCode === '' && Toast.fail('获取账户列表失败')

        this.reloadAccountInfo(this.accountCode)
      } catch (e) {
        console.info(e.message) // 异常获取
      }
    }
  }
}
</script>

<style lang="less">
.nong-home {
  width: 100%;
  color: @ft-white;
  font-size: @base-size * 1.4;
  box-sizing: border-box;
  background: url(../../assets/images/ssg/home_ssg.png) no-repeat top;
  background-size: 100% 60%;
  &--title {
    font-size: @base-size * 2.7;
    padding: 0 @base-size*2.6 @base-size*2 @base-size*2.6;
    box-sizing: inherit;
  }
  &--skeleton {
    width: 100%;
    box-sizing: inherit;
    .van-skeleton__avatar {
      width: @base-size * 3.2 !important;
      height: @base-size * 3.2 !important;
    }
    .skeleton-card {
      width: 89.33%;
      height: @base-size * 17.4;
      margin: auto;
      border-radius: @base-size * 0.85;
      background-color: @ft-white;
      padding: @base-size * 1.8 @base-size * 1.8 @base-size * 3 @base-size * 1.8;
      background: url(../../assets/images/ssg/anpay_card_ssg.png) no-repeat center;
      background-size: cover;
      box-sizing: inherit;
      display: flex;
      justify-content: space-between;
      flex-flow: column nowrap;
      .card-account {
        display: flex;
        justify-content: flex-start;
        flex-flow: row nowrap;
        align-items: center;
        .account-logo {
          width: @base-size * 3.5;
          height: @base-size * 3.5;
          background: url(../../assets/images/sly_logo.png) no-repeat center;
          background-size: cover;
          flex-shrink: 0;
        }
        .account-info {
          padding-left: @base-size * 1.3;
          color: @info-name-color-ssg;
          .info-name {
            font-size: @base-size * 1.9;
            line-height: 1em;
          }
          .info-number {
            font-size: @base-size * 1.4;
            color: @info-number-color-ssg;
            line-height: 1em;
            padding-top: @base-size / 2;
          }
        }
      }
      .tag {
        font-size: @base-size * 1.2;
        width: @base-size * 5.4;
        height: @base-size * 2.2;
        background-color: @tag-ssg;
        justify-content: flex-end;
        border-radius: @base-size*1.5 0 0 @base-size*1.5;
        box-shadow: 0 @base-size*.35 @base-size*.35 @base-size*.035 @tag-shadow-color-ssg;
        .tag-change {
          width: @base-size * 1.4;
          height: @base-size * 1.4;
          border-radius: 50%;
          background-color: @ft-white;
          color: @tag-ssg;
          margin-left: @base-size * 0.8;
          &::before {
            transform: translate(@base-size * 0.1, @base-size * 0.15);
          }
        }
      }
      .card-money {
        .account-title {
          font-size: @base-size * 1.4;
          color: @account-title-color-ssg;
          padding-bottom: @base-size * 0.8;
        }
        .money-color {
          font-weight: bold;
          font-size: @base-size * 2.4;
          color: @money-color-ssg;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
    .skeleton-domain {
      padding: @base-size*3.5 @base-size*2;
      .domain-btn {
        text-align: center;
        position: relative;
        .btn {
          font-weight: bold;
          width: 90%;
          font-size: @base-size * 1.8;
          border-radius: @base-size * 2.25;
          border: @base-size*.1 solid @btn-color-ssg;
          box-shadow: 0 @base-size*.3 @base-size*.35 @base-size*.012 @btn-shadow-color-ssg;
        }
        .recharge {
          background-color: @btn-color-ssg;
        }
        .widthdrawal {
          color: @btn-color-ssg;
          background-color: @ft-white;
        }
        .btn-label {
          font-size: @base-size * 1.2;
          color: @ft-white;
          position: absolute;
          right: @base-size * 3.2;
          top: @base-size * 0.1;
          z-index: 2;
          border: @base-size*.8 solid @btn-label-color-ssg;
          background-color: @btn-label-color-ssg;
          background-clip: content-box;
          height: @base-size * 2.5;
          border-bottom-width: @base-size * 0.5;
          border-bottom-color: transparent;
          &::before {
            content: '\81EA\52A8';
            writing-mode: rl-tb;
            position: absolute;
            top: -@base-size * 0.6;
            left: -@base-size * 0.6;
          }
        }
      }
    }
  }
  &--popup {
    box-sizing: inherit;
    padding-bottom: @base-size;
    .popup-close {
      text-align: center;
      color: @ft-black;
      .arrow-down {
        width: 100%;
        height: auto;
        padding: 0;
        margin: 0;
        border-radius: 0;
        line-height: 1em;
        background-color: transparent;
        color: @ft-black;
        border: 0;
      }
    }
    .radio-left-color {
      .van-radio__icon--checked .van-icon {
        background-color: @btn-color-ssg;
        border-color: @btn-color-ssg;
      }
    }
    .radio-title-class {
      color: @info-name-color-ssg;
      padding-left: @base-size;
      .radio-label-class {
        margin-top: 0;
        color: @info-number-color-ssg;
      }
    }
  }
}
</style>
